<template>
  <div style="margin-left:20px;margin-right:20px">
    <el-row>

      <el-col class="colGroup">

        <h4 style="margin-left: 20px;">部门列表</h4>

        <div style="margin-right: 20px; margin-left: 20px;">
          <el-form
            ref="form"
            :inline="true"
            :rules="rules"
            :model="form"
            label-width="132px"
          >

            <el-form-item
              label="编号"
              prop="id"
              placeholder="00x"
            >
              <el-input v-model="form.id"></el-input>
            </el-form-item>
            <el-form-item
              label="名称"
              prop="name"
            >
              <el-input v-model="form.name"></el-input>
            </el-form-item>
            <el-form-item
              label="级别"
              prop="nLevel"
            >
              <el-input v-model.number="form.nLevel"></el-input>
            </el-form-item>

          </el-form>
          <div>

            <el-button
              type="success"
              icon="el-icon-plus"
              @click="addClick"
            >
              添加
            </el-button>
            <el-button
              type="warning"
              icon="el-icon-delete"
              @click="deleteClick"
              :disabled="btnStatue.delete"
            >
              删除
            </el-button>

            <el-button
              :disabled="btnStatue.ok"
              @click="cancelClick()"
            >
              取消
            </el-button>

            <el-button
              type="primary"
              :disabled="btnStatue.ok"
              @click="okClick()"
            >
              确认
            </el-button>

          </div>

          <div style="margin-top:20px">
            <edit-table
              :table_ui_data="table_ui_data"
              :table_data="table_data"
              :table_disable_edit="table_disable_edit"
              :unique_id="['id']"
              @rowClick='rowClick'
              @dataChanged="tableDataChanged"
              ref="tableEd"
            >

            </edit-table>

          </div>

        </div>
      </el-col>
    </el-row>

    <tip-dialog
      @okClick='dialogClick'
      :dialog='dialogOb'
    ></tip-dialog>

  </div>
</template>

<script setup>
import { Message } from "element-ui";
import tipDialog from "../tipDialog.vue";

export default {
  components: {
    tipDialog,
  },
  data() {
    return {
      form: {
        id: "",
        name: "",
        nLevel: "",
      },
      dialogOb: {
        visible: false,
        title: "提示",
        tipMsg: "",
        trsfParm: {},
      },
      table_ui_data: [
        {
          prop: "id",
          label: "编号ID",
          disabled: true,
          sortable:true
        },
        {
          prop: "name",
          label: "名称",
        },
        {
          prop: "nLevel",
          label: "级别",
        },
      ],
      btnStatue: {
        delete: true,
        ok: true,
      },
      table_data: [],
      rules: {
        id: [{ required: true, message: "请输入ID", trigger: "blur" }],
        name: [{ required: true, message: "请输入名称", trigger: "blur" }],
        nLevel: [{ required: true, message: "请配置级别", trigger: "blur" },
         { type: "number", min: 1, max: 2, message: "大小要在1到2" },],
      },
      table_disable_edit: true,
    };
  },
  methods: {
    dialogClick() {},
    rowClick(val) {
      this.btnStatue.delete = val == undefined;
    },
    editClick() {
      this.$refs.tableEd.editAction();
    },
    addClick() {
      this.$refs["form"].validate((valid) => {
        if (valid) {
          this.$refs.tableEd.addAction(this.form);
        }
      });
    },
    cancelClick() {
      this.table_data = JSON.parse(
        JSON.stringify(this.$refs.tableEd.currentTableData)
      );

      this.btnStatue.ok = true;
    },
    okClick() {
      this.$refs.tableEd.confirmOKAction((obj, typeNum) => {
        if (typeNum == 1) {
          //增加
          console.log("add", obj);
          this.$httpApi.depart_add(obj).then((resp) => {
            if (resp.data.code != "0") {
              Message.error(resp.data.message);
            }
          });
        } else if (typeNum == 3) {
          //删除
          console.log("del");
          this.$httpApi.depart_del(obj).then((resp) => {
            if (resp.data.code != "0") {
              Message.error(resp.data.message);
            }
          });
        }
      });

      this.btnStatue.ok = true;
    },
    deleteClick() {
      this.$refs.tableEd.deleteAction();
    },
    tableDataChanged(val) {
      this.btnStatue.ok = val == 0;
    },
  },
  mounted() {
    var that = this;
    this.$httpApi.depart_list().then((res) => {
      if (res.data.code == "0") {
        
        that.table_data = res.data.dataList ? res.data.dataList:new Array;
        that.$refs.tableEd.initCurrentData(that.table_data);
      } else {
        Message.error(res.data.message);
      }
    });
  },
};
</script>

<style lang="scss" scoped>
.colGroup {
  border: 1px solid lightgray;
  height: 100vh;
  margin-bottom: 20px;
}
</style>